.wpp-credit-checkout .line {
  margin: 10px 0;
}

.wpp-credit-checkout .form-row input {
  font-size: 18px;
}

.wpp-credit-checkout .select {
  padding-top: 10px;
}

.wpp-credit-checkout .select select {
  width: 100%;
  height: 35px;
}

.wpp-credit-checkout .wpp-card-owner input {
  text-transform: uppercase;
}

.wpp-credit-checkout .wpp-brands-section {
  margin-bottom: 15px;
}

.wpp-credit-checkout .wpp-brands-section > .label {
  justify-content: center;
  align-items: center;
  margin: 5px 0;
  display: flex;
}

.wpp-credit-checkout .wpp-brands-section > .label label {
  font-weight: bold;
}

.wpp-credit-checkout .wpp-brands-section > .brands {
  grid-gap: 5px;
  grid-template-columns: repeat(5, 1fr);
  display: grid;
}

.wpp-credit-checkout .wpp-brands-section > .brands img {
  margin: 0 5px;
}

.wpp-credit-checkout .wpp-card-img {
  position: relative;
}

.wpp-credit-checkout .wpp-card-img img {
  position: absolute;
  top: 10px;
  right: 10px;
}

.wpp-credit-checkout .wpp-card-img input {
  padding-right: 60px;
}

